import React from 'react';

class ChangingCaption extends React.Component {

    captions = ["Tacos", "Dumplings", "Noodles", "Corbin!"]

    constructor(props) {
      super(props);
      this.state = {date: new Date()};
    }

    componentDidMount() {
        this.timerID = setInterval(
          () => this.tick(),
          1000
        );
    }
    
    componentWillUnmount() {
        clearInterval(this.timerID);
    }
    
    tick() {
        this.setState({
          date: new Date()
        });
    }

    render() {
      return (
        <div>
          <p style={{"color": "#3ded97"}} >Likes {this.captions[this.state.date.getUTCSeconds() % 4]}
          </p>
        </div>
      );
    }
  }

  export default ChangingCaption;