import React from 'react';
import ItemImage from './ItemImage.js'

class ItemBox extends React.Component {
    constructor(props){
        super(props);
        this.description = 
            <div> 
                <h3>{props.projects[props.id].title}</h3>
                <p>{props.projects[props.id].content}</p>
            </div>;
    }

    render() {
      return (
        <div className="boxed">
          <ItemImage />
          {this.description}
        </div>
      );
    }
  }

  export default ItemBox;